@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap');

body {
    font-family: 'Playfair Display', 'Georgia', serif;
    margin: 0;
    padding: 0;
    background-color: #fff;
    color: #444;
    min-height: 100vh;
    letter-spacing: 0.02em;
}

h1, h2, h3 {
    font-family: 'Playfair Display', 'Georgia', serif;
    color: #bfa980;
    text-align: center;
    font-weight: 700;
    margin-top: 2rem;
    margin-bottom: 1rem;
}

p {
    text-align: center;
    color: #666;
    font-size: 1.1rem;
    margin-bottom: 1.5rem;
}

/* Fondo sutil para secciones */
.section {
    background: #faf7f2;
    border-radius: 16px;
    box-shadow: 0 2px 12px rgba(0,0,0,0.04);
    margin: 2rem auto;
    padding: 2rem 1.5rem;
    max-width: 700px;
}

/* Botón elegante */
.btn {
    background: #bfa980;
    color: #fff;
    border: none;
    border-radius: 24px;
    padding: 0.75rem 2.5rem;
    font-size: 1.1rem;
    font-family: 'Playfair Display', 'Georgia', serif;
    cursor: pointer;
    transition: background 0.2s;
    margin: 1rem auto;
    display: block;
}
.btn:hover {
    background: #a08c5b;
}

/* Separador decorativo */
.divider {
    width: 80px;
    height: 2px;
    background: #bfa980;
    margin: 2rem auto;
    border-radius: 2px;
    opacity: 0.3;
}